.TransactionDetails{margin-bottom:25px;display:block}.TransactionDetails-row{display:flex;align-items:center}.TransactionDetails-row.stacked{flex-direction:column;align-items:flex-start}.TransactionDetails-row.border{border-bottom:1px solid #e8eaed;padding-top:5px;padding-bottom:5px}.TransactionDetails-row:last-child{border:0}.TransactionDetails-row>div{padding:7px 0px}.TransactionDetails-row.stacked .TransactionDetails-row-column+.TransactionDetails-row-data{padding-top:0px}.TransactionDetails-row-column{flex:1}.TransactionDetails-row-column:first-child{font-weight:bold}.TransactionDetails-row-column:nth-child(2){text-align:right}.TransactionDetails-row-data{width:100%;display:inline-block;word-break:break-word}.TransactionDetails-row-data-empty{width:auto}.TransactionDetails-detailButton{width:100%;align-items:center;margin-bottom:15px;color:#1eb8e7;background-color:#fff;font-weight:normal}.TransactionDetails-detailButton :hover{background-color:#fff}