.GasPriceSlider{margin-top:0;margin-bottom:0}.GasPriceSlider-input-group{display:flex;justify-content:center;align-items:center}.GasPriceSlider-input-group>.GasPriceSlider-slider{width:calc(100% - 12px);margin-left:calc(-4px)}.GasPriceSlider-input-group>.GasPriceSlider-slider>.rc-slider>.rc-slider-step>.rc-slider-dot{height:12px;width:12px;bottom:-3px;background-color:#fff;border:solid 1px rgba(102,170,229,.3);box-shadow:0 1px 1px 0 rgba(232,234,237,.5)}.GasPriceSlider-input-group>.GasPriceSlider-slider>.rc-slider>.rc-slider-rail{background-color:#1eb8e7;opacity:.22}.GasPriceSlider-input-group>.GasPriceSlider-slider>.rc-slider>.rc-slider-track{background-color:#1eb8e7;visibility:visible !important;opacity:1}.GasPriceSlider-input-group>.GasPriceSlider-slider>.rc-slider>.rc-slider-handle{width:20px;height:20px;background-color:#fff;box-shadow:0 1px 1px 0 rgba(232,234,237,.5);border:solid 1px #1eb8e7}.GasPriceSlider-input-group>.FeeSummary{margin-left:1rem;min-width:224px}@media screen and (max-width: 1060px){.GasPriceSlider-input-group{flex-wrap:wrap}.GasPriceSlider-input-group>.FeeSummary{width:100%;margin-left:0}}.GasPriceSlider-slider{padding-top:8px;margin-bottom:.25rem}.GasPriceSlider-slider-labels{margin-top:4px;display:flex}.GasPriceSlider-slider-labels>span{flex:1;padding:0;text-align:center;color:rgba(var(--color-dark-rgb), 0.4);font-size:.75rem}.GasPriceSlider-slider-labels>span:first-child{text-align:left;margin-left:calc(-2px)}.GasPriceSlider-slider-labels>span:last-child{text-align:right;margin-right:calc(-6px)}